<template>
  <div>
  <navigation-bar  :pageName="title" ref="navigation" @onLeftClick="goBack"></navigation-bar>
    <iframe
        id="my-iframe"
        :src="advHtml"
        frameborder="0"
        class="ifreamContent"
      />
  </div>
</template>
<script>
    import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
    data(){
        return{
            advHtml:"",
            title:"",
        }
    },
      components:{
        NavigationBar
        },
    methods:{
        goBack(){
            this.$router.replace("/productPage");
        }
    },
    mounted(){
        console.log("跳转至第三方链接页面"+this.$route.params.title);
        this.advHtml=this.$route.params.advHtml;
        this.title=this.$route.params.title;
    }
}    
</script>
<style scope>
.ifreamContent{
    height:-webkit-fill-available;
    width:100%;
}
</style>